<template>
    <div id="Sheet">
        
        <div>
            <h4><label>Level: </label>
            <input v-model.number="level" type="number" min="1" max="18" step="1" value="1" size="12"></h4>

            <h4>Stats: </h4>

            <table>
                <tr>
                    <td> HP: </td>
                    <td> {{ currentHP }} </td>
                </tr>
                <tr>
                    <td> MP: </td>
                    <td> {{ currentMP }} </td>
                </tr>
                <br>
                  <tr>
                    <td> HP regen: </td>
                    <td> {{ currentHpRegen }} </td>
                </tr>
                  <tr>
                    <td> MP regen: </td>
                    <td> {{ currentMpRegen }} </td>
                </tr>
                <br>
                  <tr>
                    <td> Armor: </td>
                    <td> {{ currentArmor }} </td>
                </tr>
                  <tr>
                    <td> Spell Block: </td>
                    <td> {{ currentSpellBlock }} </td>
                </tr>
                <br>
                  <tr>
                    <td> Crit: </td>
                    <td> {{ currentCrit }} </td>
                </tr>
                  <tr>
                    <td> Attack Dmg </td>
                    <td> {{ currentAttackDamage }} </td>
                </tr>
                <tr>
                    <td> Attack Spd </td>
                    <td> {{ currentAttackSpeed }} </td>
                </tr>
            </table>

        </div>

        <div id="inv">
        </div>

    </div>
</template>


<script>


export default {
    props: ['champ'],
    
    data() {
        return {  
            level: null
        }
    },

    computed: {
        currentHP: function() {
            return this.champ.stats.hp + (this.level * this.champ.stats.hpperlevel);
        },
        currentMP: function() {
            return this.champ.stats.mp + (this.level * this.champ.stats.mpperlevel);
        },
        currentArmor: function() {
            return (this.champ.stats.armor + (this.level * this.champ.stats.armorperlevel)).toFixed(3);
        },
        currentSpellBlock: function() {
            return (this.champ.stats.spellblock + (this.level * this.champ.stats.spellblockperlevel)).toFixed(3);
        },
        currentHpRegen: function() {
            return (this.champ.stats.hpregen + (this.level * this.champ.stats.hpregenperlevel)).toFixed(3);
        },
        currentMpRegen: function() {
            return (this.champ.stats.mpregen + (this.level * this.champ.stats.mpregenperlevel)).toFixed(3);
        },
        currentCrit: function() {
            if(this.champ.stats.crit == 0)
            {
                return '';
            }
            else return this.champ.stats.crit + (this.level * this.champ.stats.critlevel);
        },
        currentAttackDamage: function() {
            return (this.champ.stats.attackdamage + (this.level * this.champ.stats.attackdamageperlevel)).toFixed(3);
        },
        currentAttackSpeed: function() {
            return (this.champ.stats.attackspeed + (this.level * this.champ.stats.attackspeedperlevel)).toFixed(3);
        },
    },
}

</script>


<style scoped lang="scss">


</style>